import { createStructuredSelector } from 'reselect';

// Action Types
const INIT = 'helpwith19/apply/INIT';

export const NAME = 'apply';

const initialState = {
   apply: ""
};

export default function reducer(state = initialState, action ={}) {
   
  switch (action.type) {
    
    case INIT:
        return {
            ...state
        }

    default:
      return state;
  }
}

// Action Creators
function init(){
    return {
        type: INIT
    }
}

const apply = (state) => state[NAME].apply;


export const selector = createStructuredSelector({
    apply
});

export const actionCreators = {
    init
};