import React from 'react';
import { Provider } from 'react-redux';

import { Button, Intent, Spinner, Navbar, Alignment } from "@blueprintjs/core";
 // using node-style package resolution in a CSS file: 
import Home from "./features/home/components";
import Apply from "./features/apply/components";
import Volunteer from "./features/volunteer/components";
import Resources from "./features/resources/components";

import "@blueprintjs/core/lib/css/blueprint.css";
import "@blueprintjs/icons/lib/css/blueprint-icons.css";
import './App.css';
import configureStore from './store/configureStore';
import banner from './banner.svg';
// using JSX:
import {
  HashRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

const store = configureStore();
//const history = syncHistoryWithStore(browserHistory, store);

export default function App() {
  return (
    <Provider store={store}>
      <Router>

        <Navbar className="bp3-dark">
          <Navbar.Group align={Alignment.LEFT}>
            <Navbar.Heading>Help with Covid-19</Navbar.Heading>
            <Navbar.Divider />
            <Link to="/"><button className="bp3-button bp3-minimal">home </button></Link>
            <a href="https://docs.google.com/forms/d/e/1FAIpQLSeEg2b0Rhxk6j9HSitv_2TxM5Yjdy1hulU__9rwbUlhMtk2CA/viewform?usp=sf_link"><button className="bp3-button bp3-minimal">apply</button></a> 
            <a href="https://docs.google.com/forms/d/e/1FAIpQLSc85htptLwAdCHkFE9lHY_5ugBDyslBshQTubyCX0Je57fqtg/viewform?usp=sf_link"><button className="bp3-button bp3-minimal">volunteer</button></a>
          
          </Navbar.Group>
        </Navbar>
        <img className="banner" src={banner}></img>
        <>

          {/* A <Switch> looks through its children <Route>s and
              renders the first one that matches the current URL. */}
          <Switch>
            <Route path="/apply">
              <Apply/>
            </Route>
            <Route path="/volunteer">
              <Volunteer />
            </Route>
            <Route path="/users">
              <Users />
            </Route>
            <Route path="/resources">
              <Resources />
            </Route>
            <Route path="/">
              <Home />
            </Route>
            <Route path="*">
              <Home />
            </Route>
          </Switch>
          
          </>
      </Router>
    </Provider>
  );
}



function About() {
  return <h2>About</h2>;
}

function Users() {
  return <>
    <h2>Users</h2>;
    <Spinner intent={Intent.PRIMARY} />
  </>
}
