import { createStructuredSelector } from 'reselect';

// Action Types
const INIT = 'helpwith19/resources/INIT';

export const NAME = 'resources';

const initialState = {
   home: ""
};

export default function reducer(state = initialState, action ={}) {
   
  switch (action.type) {
    
    case INIT:
        return {
            ...state
        }

    default:
      return state;
  }
}

// Action Creators
function init(){
    return {
        type: INIT
    }
}

const resources = (state) => state[NAME].volunteer;


export const selector = createStructuredSelector({
    resources
});

export const actionCreators = {
    init
};