import { combineReducers } from 'redux';
import { routerReducer as routing } from 'react-router-redux';


import home, { NAME as homeName } from './features/home';
import apply, { NAME as applyName } from './features/apply';
import resources, { NAME as resourcesName } from './features/resources';
import volunteer, {NAME as volunteerName} from './features/volunteer';
export default combineReducers({
  routing,
  [homeName]: home,
  [applyName]: apply,
  [resourcesName]: resources,
  [volunteerName]: volunteer,
});
