import React, { Component, useEffect } from 'react';
import {connect} from 'react-redux';
import { actionCreators as applyActions, selector } from '../';
import { bindActionCreators } from 'redux';
import { Divider, FormGroup, InputGroup, TextArea, Intent , Button} from "@blueprintjs/core";

import {
    Link
} from "react-router-dom";

function Apply() {
    let frame;

    useEffect(() => {
        frame.scrollTo(0, 0)
    }, [])

    return <div className="content">
        <h3>Apply for help</h3>
        <Divider horizontal="true"/>
        <div style={{marginTop:10, lineHeight:1.7, overflow:"auto"}}>
        <iframe ref={(c)=>{frame = c}} src="https://docs.google.com/forms/d/e/1FAIpQLSeEg2b0Rhxk6j9HSitv_2TxM5Yjdy1hulU__9rwbUlhMtk2CA/viewform?embedded=true" width="100%" height="600px" frameborder="0" marginheight="0" marginwidth="0">Loading…</iframe>
        </div>
    </div>
}

export default connect(selector, (dispatch) => ({
    actions: bindActionCreators(applyActions, dispatch)
}))(Apply);