import { createStructuredSelector } from 'reselect';

// Action Types
const INIT = 'helpwith19/home/INIT';

export const NAME = 'home';

const initialState = {
   home: ""
};

export default function reducer(state = initialState, action ={}) {
   
  switch (action.type) {
    
    case INIT:
        return {
            ...state
        }

    default:
      return state;
  }
}

// Action Creators
function init(){
    return {
        type: INIT
    }
}

const home = (state) => state[NAME].home;


export const selector = createStructuredSelector({
    home
});

export const actionCreators = {
    init
};