import React, { Component, useEffect } from 'react';
import {connect} from 'react-redux';
import { actionCreators as volunteerActions, selector } from '../';
import { bindActionCreators } from 'redux';
import { Divider } from "@blueprintjs/core";

import {
    Link
} from "react-router-dom";

function Volunteer() {
    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])

    return <div className="content">
        <h3>Thanks for considering volunteering!</h3>
        <Divider horizontal="true"/>
        <div style={{marginTop:10, lineHeight:1.7}}>
        <iframe src="https://docs.google.com/forms/d/e/1FAIpQLSc85htptLwAdCHkFE9lHY_5ugBDyslBshQTubyCX0Je57fqtg/viewform?embedded=true" width="100%" height="1464" frameborder="0" marginheight="0" marginwidth="0">Loading…</iframe>
        </div>
       
    </div>
}

export default connect(selector, (dispatch) => ({
    actions: bindActionCreators(volunteerActions, dispatch)
}))(Volunteer);